import React, { useContext, useEffect } from "react";
import { AuthContext } from "../../contexts/AuthContext";
import classnames from "classnames";

import styles from "./UserMenu.module.css";

export default function UserMenu({ userMenu, onClose, __ref }) {
  const context = useContext(AuthContext);

  useEffect(() => {
    if (!userMenu) return;

    const handleClick = (event) => {
      if (!__ref.current) return;
      if (!__ref.current.contains(event.target)) {
        onClose();
      }
    };

    document.addEventListener("click", handleClick);
    return () => document.removeEventListener("click", handleClick);
  }, [onClose, userMenu, __ref]);

  return (
    <div className={classnames(styles.userMenu, userMenu && styles.active)}>
      <div className={styles.userWrapper}>
        <div className={styles.userName}>hello, ALALAH!</div>
        <button className={styles.userLogout} onClick={() => context.logout()}>
          log out
        </button>
      </div>
    </div>
  );
}
