import React from "react";
// import { AuthContext } from "../../contexts/AuthContext";
//
import styles from "./Categories.module.css";

import TabUnstyled from "@mui/base/TabUnstyled";
import TabsListUnstyled from "@mui/base/TabsListUnstyled";

import { styled } from "@mui/material/styles";

const HeaderButton = styled(TabUnstyled)(({ theme }) => ({
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "flex-end",
  "& > :last-of-type": {
    margin: "4px 4px",
    fontFamily: "var(--Inter-Regular)",
    fontSize: "14px",
    lineHeight: "143%",
    letterSpacing: "0.018em",
    textAlign: "center",
    color: "#787885",
  },
  "&:nth-of-type(1) > :last-of-type": { width: "136px" },
  "&:nth-of-type(2) > :last-of-type": { width: "116px" },
  "&:nth-of-type(3) > :last-of-type": { width: "64px" },
  "&:nth-of-type(4) > :last-of-type": { width: "130px" },
  "&:nth-of-type(5) > :last-of-type": { width: "56px" },
  "&:nth-of-type(6) > :last-of-type": { width: "154px" },
  "&:nth-of-type(7) > :last-of-type": { width: "136px" },
  "&:nth-of-type(8) > :last-of-type": { width: "140px" },
  "&:nth-of-type(9) > :last-of-type": { width: "122px" },
  "&:nth-of-type(10) > :last-of-type": { width: "106px" },
  "&:nth-of-type(11) > :last-of-type": { width: "164px" },
  "&:nth-of-type(12) > :last-of-type": { width: "72px" },

  "&.Mui-selected": {
    "& > :first-of-type path": {
      fill: "#2979FF",
    },
    "& > :last-of-type": {
      color: "#2979FF",
    },
  },
}));

export default function Categories(props) {
  return (
    <>
      <TabsListUnstyled className={styles.categoriesList}>
        {props.items.map((item) => (
          <HeaderButton key={item.cat_id} value={item.cat_id}>
            <div className={styles.icon}>{item.image}</div>
            <div className={styles.title}>{item.title}</div>
          </HeaderButton>
        ))}
      </TabsListUnstyled>
    </>
  );
}
