import classNames from "classnames";
import React, { useEffect, useRef, useState } from "react";

import styles from "./Tree.module.css";

const Tree = () => {
  const MAX_TO_SHOW = 6;
  const [expandMax, setExpandMax] = useState(6);

  const filters = [
    {
      parent: true,
      title: "All Categories",
      subtitle: "Ecommerce patterns",
      children: [
        {
          title: "Electronics",
          children: [
            {
              title: "Cell Phones & Smartphones",
              children: [
                { title: "Cell Phone Accessories" },
                { title: "Applications" },
                { title: "Smart Watches" },
                { title: "Cell Phone Gatgets" },
              ],
            },
            {
              title: "Business & Industrial",
              children: [{ title: "0" }, { title: "00" }, { title: "000" }],
            },
            {
              title: "Computers",
              subtitle: "Tablets & Networking",
              children: [{ title: "1" }, { title: "11" }, { title: "111" }],
            },
            {
              title: "Consumer Electroinics",
              children: [{ title: "2" }, { title: "22" }, { title: "222" }],
            },
            {
              title: "Home & Garden",
              children: [{ title: "3" }, { title: "33" }, { title: "333" }],
            },
            {
              title: "Collectibles",
              children: [{ title: "4" }, { title: "44" }, { title: "444" }],
            },
            {
              title: "abcdef",
              children: [{ title: "5" }, { title: "55" }, { title: "555" }],
            },
            {
              title: "ABCDEF",
              children: [{ title: "6" }, { title: "66" }, { title: "666" }],
            },
          ],
        },
      ],
    },
  ];

  const ref = useRef(null);

  const toogleExpandMax = (max) => {
    setExpandMax(expandMax !== MAX_TO_SHOW ? MAX_TO_SHOW : max);
  };

  useEffect(() => {
    ref.current.onclick = function (event) {
      if (event.target.tagName !== "SPAN") return;

      let childrenContainer;
      if (event.target.classList[0] === "span-child") {
        childrenContainer =
          event.target.parentNode.parentNode.querySelector("ul");
      } else {
        childrenContainer = event.target.parentNode.querySelector("ul");
      }

      if (!childrenContainer) return;

      childrenContainer.classList.toggle("list-hide");

      if (event.target.classList[0] === "span-child") {
        if (childrenContainer.classList.contains("list-hide")) {
          event.target.parentNode.classList.add("hide");
          event.target.parentNode.classList.remove("show");
        } else {
          event.target.parentNode.classList.add("show");
          event.target.parentNode.classList.remove("hide");
        }
      } else {
        if (childrenContainer.classList.contains("list-hide")) {
          event.target.classList.add("hide");
          event.target.classList.remove("show");
        } else {
          event.target.classList.add("show");
          event.target.classList.remove("hide");
        }
      }
    };
  }, []);

  function getTree(list, next_expand, expand) {
    return list.map((item, index, arr) => {
      const treeProps = {};
      if (item.parent) {
        treeProps.className = styles.tree;
        treeProps.id = "tree";
        treeProps.ref = ref;
      }

      let spanChild = null;
      if (item.subtitle) {
        spanChild = (
          <span className="hide span-parent">
            <span className="span-child">{item.title}</span>
            <span className="span-child">{item.subtitle}</span>
          </span>
        );
      }

      let body = (
        <>
          {index < expandMax ? (
            <>
              <li>
                {spanChild ? (
                  spanChild
                ) : (
                  <span className={!item.children ? "last-child" : "hide"}>
                    {item.title}
                  </span>
                )}
                {item.children && item.children.length > 0 && (
                  <ul className="list-hide">
                    {getTree(
                      item.children,
                      item.parent ? true : false,
                      next_expand ? true : false
                    )}
                  </ul>
                )}
              </li>
              {!item.children && arr.length - 1 === index && (
                <button className={classNames(styles.seeMore, styles.button)}>
                  Show more
                </button>
              )}
            </>
          ) : null}
          {expand && arr.length - 1 === index && arr.length > MAX_TO_SHOW && (
            <button
              className={classNames(styles.expand, styles.button)}
              onClick={() => {
                toogleExpandMax(arr.length);
              }}
            >
              {MAX_TO_SHOW < expandMax ? "Hide" : "Expand"}
            </button>
          )}
        </>
      );

      return item.parent ? (
        <ul key={item.title} {...treeProps}>
          {body}
        </ul>
      ) : (
        <React.Fragment key={item.title}>{body}</React.Fragment>
      );
    });
  }

  return getTree(filters);
};

export default Tree;
