import React from "react";

import Tree from "./Tree/Tree";

import styles from "./Filter.module.css";
import { filter_variant } from "@/svg";
import Filters from "./Filters/Filters";

export default function Filter(props) {
  const filters = [
    {
      title: "Collapsed filters",
      filters: [
        { title: "some 1", checked: false },
        { title: "some 2", checked: false },
        { title: "some 3", checked: false },
        { title: "some 4", checked: false },
        { title: "some 5", checked: false },
        { title: "some 6", checked: false },
      ],
    },
    {
      title: "Expanded Filters",
      filters: [
        { title: "Recommended", checked: false },
        { title: "Recently Added", checked: false },
        { title: "Expiring Soon", checked: false },
        { title: "Most Rated", checked: false },
        { title: "Price: Low → High", checked: false },
        { title: "Price: High → Low", checked: false },
      ],
    },
    {
      title: "Year of manufacturing",
      filters: [
        { title: "1954", checked: false },
        { title: "1955", checked: false },
        { title: "1956", checked: false },
        { title: "1957", checked: false },
        { title: "1958", checked: false },
        { title: "1959", checked: false },
        { title: "1960", checked: false },
        { title: "1961", checked: false },
        { title: "1962-2020", checked: false },
      ],
    },
  ];

  return (
    <aside className={props.className}>
      <div className={styles.departaments}>
        {filter_variant()}
        <span>Departments</span>
      </div>
      <div className={styles.tree}>
        <Tree />
      </div>
      <div className={styles.filters}>
        <Filters filters={filters} />
      </div>
    </aside>
  );
}
