import React, { useState } from "react";
import styles from "./Sorts.module.css";

import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select, { SelectChangeEvent } from "@mui/material/Select";

import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";

import ViewListIcon from "@mui/icons-material/ViewList";
import GridViewSharpIcon from "@mui/icons-material/GridViewSharp";

import { styled } from "@mui/material/styles";

const StyledFormControl = styled(FormControl)(({ theme }) => ({
  "& .MuiInputBase-root": {
    height: "36px",
  },
  "& .MuiInputLabel-formControl": {
    top: "50%",
    transform: "translateY(-50%) scale(1)",
    left: "12px",
    fontFamily: "var(--Inter-Regular)",
    fontSize: "14px",
    lineHeight: "143%",
    letterSpacing: "0.018em",
    color: "#787885",
    transition: "all 200ms cubic-bezier(0.0, 0, 0.2, 1)",
    "&.Mui-focused": {
      left: 0,
      top: 0,
      transform: "translate(14px, -9px) scale(0.75)",
      transition: "all 200ms cubic-bezier(0.0, 0, 0.2, 1)",
    },
    "&.MuiFormLabel-filled": {
      left: 0,
      top: 0,
      transform: "translate(14px, -9px) scale(0.75)",
      transition: "all 200ms cubic-bezier(0.0, 0, 0.2, 1)",
      "& ~ div > fieldset": {
        borderColor: "#5A5B6A",
      },
    },
  },
  "& .MuiSelect-select.MuiInputBase-input": {
    padding: "6px 12px",
    fontFamily: "var(--Inter-Medium)",
    fontSize: "14px",
    lineHeight: "143%",
    letterSpacing: "0.018em",
    color: "#19191D",
  },
}));
const ListTabs = styled(Tabs)(({ theme }) => ({
  overflow: "visible",
  minHeight: "36px",
  marginRight: "24px",
  borderRadius: "4px",
  "& *": {
    overflow: "visible !important",
  },
  "& .MuiTab-root": {
    background: "#EBF2FF",

    width: "85px",
    minHeight: "36px",
    padding: "0px",
    fontFamily: "var(--Inter-Regular)",
    fontSize: "14px",
    lineHeight: "143%",
    letterSpacing: "0.018em",
    textTransform: "capitalize",
    color: "#5A5B6A",
    transition: "all 200ms cubic-bezier(0.0, 0, 0.2, 1)",
    "&.Mui-selected": {
      background: "white",

      fontFamily: "var(--Inter-Medium)",
      color: "#2264D1",
      textTransform: "capitalize",
      boxShadow:
        "0px 1px 2px rgba(27, 78, 163, 0.24), 0px 2px 4px rgba(41, 121, 255, 0.24)",
      borderRadius: "4px",
    },
    "& .MuiTouchRipple-root": {
      overflow: "hidden !important",
    },
  },
  "& .MuiTabs-indicator": {
    display: "none",
  },
}));
const ListGrid = styled(Tabs)(({ theme }) => ({
  overflow: "visible",
  minHeight: "36px",
  borderRadius: "4px",
  "& *": {
    overflow: "visible !important",
  },
  "& .MuiTab-root": {
    background: "#EBF2FF",

    width: "85px",
    minHeight: "36px",
    padding: "0px",
    color: "#5A5B6A",
    transition: "all 200ms cubic-bezier(0.0, 0, 0.2, 1)",
    "&.Mui-selected": {
      background: "white",

      color: "#2979FF",
      boxShadow:
        "0px 1px 2px rgba(27, 78, 163, 0.24), 0px 2px 4px rgba(41, 121, 255, 0.24)",
      borderRadius: "4px",
    },
    "& .MuiTouchRipple-root": {
      overflow: "hidden !important",
    },
  },
  "& .MuiTabs-indicator": {
    display: "none",
  },
}));

const Sorts = (props) => {
  const [sortBy, setSortBy] = useState("");
  const [condition, setCondition] = useState("");
  const [deliveryOptions, setDeliveryOptions] = useState("");

  const [sortList, setSortList] = useState(0);

  return (
    <div className={styles.header}>
      <div className={styles.sorts}>
        <StyledFormControl className={styles.sort1}>
          <InputLabel id="demo-simple-select-autowidth-label">
            SORT BY
          </InputLabel>
          <Select
            labelId="demo-simple-select-autowidth-label"
            id="demo-simple-select-autowidth"
            value={sortBy}
            onChange={(e) => {
              setSortBy(e.target.value);
            }}
            autoWidth
            label="SORT BY"
          >
            <MenuItem value={""}>
              <em>None</em>
            </MenuItem>
            <MenuItem value={10}>Twenty</MenuItem>
            <MenuItem value={21}>Twenty one</MenuItem>
            <MenuItem value={22}>Twenty one and a half</MenuItem>
          </Select>
        </StyledFormControl>
        <StyledFormControl className={styles.sort2}>
          <InputLabel id="demo-simple-select-autowidth-label">
            Condition
          </InputLabel>
          <Select
            labelId="demo-simple-select-autowidth-label"
            id="demo-simple-select-autowidth"
            value={condition}
            onChange={(e) => {
              setCondition(e.target.value);
            }}
            autoWidth
            label="Condition"
          >
            <MenuItem value={""}>
              <em>None</em>
            </MenuItem>
            <MenuItem value={10}>Twenty</MenuItem>
            <MenuItem value={21}>Twenty one</MenuItem>
            <MenuItem value={22}>Twenty one and a half</MenuItem>
          </Select>
        </StyledFormControl>
        <StyledFormControl className={styles.sort3}>
          <InputLabel id="demo-simple-select-autowidth-label">
            Delivery options
          </InputLabel>
          <Select
            labelId="demo-simple-select-autowidth-label"
            id="demo-simple-select-autowidth"
            value={deliveryOptions}
            onChange={(e) => {
              setDeliveryOptions(e.target.value);
            }}
            autoWidth
            label="Delivery options"
          >
            <MenuItem value={""}>
              <em>None</em>
            </MenuItem>
            <MenuItem value={10}>Twenty</MenuItem>
            <MenuItem value={21}>Twenty one</MenuItem>
            <MenuItem value={22}>Twenty one and a half</MenuItem>
          </Select>
        </StyledFormControl>
      </div>
      <div className={styles.sortsTabs}>
        <ListTabs value={sortList} onChange={(_, v) => setSortList(v)}>
          <Tab label="Show all" value={0} aria-label="view-list" />
          <Tab label="Auction" value={1} aria-label="view-list" />
          <Tab label="Buy now" value={2} aria-label="view-list" />
        </ListTabs>
        <ListGrid
          value={props.sortGrid}
          onChange={(_, v) => props.setSortGrid(v)}
          centered
        >
          <Tab value={0} icon={<ViewListIcon />} aria-label="view-grid" />
          <Tab value={1} icon={<GridViewSharpIcon />} aria-label="view-grid" />
        </ListGrid>
      </div>
    </div>
  );
};

export default Sorts;
