import classnames from "classnames";
import React from "react";
import styles from "./Related.module.css";

const Related = (props) => {
  const data = [
    {
      id: 0,
      title: "worldwide shipping",
    },
    {
      id: 1,
      title: "under $50",
    },
    {
      id: 2,
      title: "kitten",
    },
    {
      id: 3,
      title: "plastic plugs",
    },
    {
      id: 4,
      title: "pucker shoes",
    },
    {
      id: 5,
      title: "vintage typewriter",
    },
  ];

  return (
    <div className={classnames(props.className, styles.related)}>
      <div className={styles.relatedTitle}>Related</div>
      {data.map((item) => (
        <div className={styles.relatedClips} key={item.id}>
          {item.title}
        </div>
      ))}
    </div>
  );
};

export default Related;
