import React from "react";

import styles from "./Body.module.css";

import TabsUnstyled from "@mui/base/TabsUnstyled";
import TabPanelUnstyled from "@mui/base/TabPanelUnstyled";

import {
  hanger,
  cinema,
  concert,
  fitness,
  pets,
  restaraunt,
  observation,
  barley,
  powerstation,
  babysitter,
  nuclear_station,
  ship_wheel,
} from "../../svg";

import { Categories } from "../Categories";
import { Filter } from "../Filter";
import { ProductsTable } from "../ProductsTable";

export default function Body() {
  const categories = [
    {
      image: hanger(),
      title: "Clothing & Shoes",
      cat_id: 1,
    },
    {
      image: cinema(),
      title: "Entertainment",
      cat_id: 2,
    },
    {
      image: concert(),
      title: "Music",
      cat_id: 3,
    },
    {
      image: fitness(),
      title: "Sport & Lifestyle",
      cat_id: 4,
    },
    {
      image: pets(),
      title: "Pets",
      cat_id: 5,
    },
    {
      image: restaraunt(),
      title: "Kitchen Accessories",
      cat_id: 6,
    },
    {
      image: observation(),
      title: "Travel Equipment",
      cat_id: 7,
    },
    {
      image: barley(),
      title: "Growing & Garden",
      cat_id: 8,
    },
    {
      image: powerstation(),
      title: "Electrical Tools",
      cat_id: 9,
    },
    {
      image: babysitter(),
      title: "Mother Care",
      cat_id: 10,
    },
    {
      image: nuclear_station(),
      title: "Toys & Entertainment",
      cat_id: 11,
    },
    {
      image: ship_wheel(),
      title: "Vintage",
      cat_id: 12,
    },
  ];

  return (
    <div className={styles.categories}>
      <TabsUnstyled defaultValue={1} className={styles.dd}>
        <Categories items={categories} />
        <div className={styles.body}>
          <Filter className={styles.filter} />
          <main className={styles.products}>
            {categories.map((item) => (
              <TabPanelUnstyled key={item.cat_id} value={item.cat_id}>
                <ProductsTable cat_id={item.cat_id} />
              </TabPanelUnstyled>
            ))}
          </main>
        </div>
      </TabsUnstyled>
    </div>
  );
}
