import React, { useContext, useRef, useState } from "react";
import { AuthContext } from "../../contexts/AuthContext";

import styles from "./Header.module.css";
import classnames from "classnames";

import logo from "@/images/logo.svg";
import cart from "@/images/cart.svg";
import user from "@/images/user.svg";
import search from "@/images/search.svg";
import cross from "@/images/cross.svg";
import { UserMenu } from "../UserMenu";

export default function Header() {
  const context = useContext(AuthContext);
  const [searchValue, setSearchValue] = useState("");
  const [userMenu, setUserMenu] = useState(false);

  const onClose = () => {
    setUserMenu(false);
  };
  const __ref = useRef(null);

  return (
    <div className={styles.header}>
      <img src={logo} alt="logo" />

      <div className={styles.search}>
        <img className={styles.iconSearch} src={search} alt="search" />

        <input
          type="text"
          name=""
          id=""
          value={searchValue}
          onChange={(e) => setSearchValue(e.target.value)}
          placeholder="Input some text..."
        />

        {searchValue.length > 0 && (
          <img
            onClick={() => setSearchValue("")}
            className={styles.iconCross}
            src={cross}
            alt="cross"
          />
        )}
      </div>

      <div className={styles.user}>
        <div className={styles.userCart}>
          <img src={cart} alt="cart" />
        </div>

        {context.auth ? (
          <div
            className={classnames(
              styles.user,
              userMenu && styles.userMenuActive
            )}
            ref={__ref}
          >
            <div
              className={styles.userLogo}
              onClick={() => setUserMenu(!userMenu)}
            >
              <img src={user} alt="user" />
            </div>
            <UserMenu __ref={__ref} userMenu={userMenu} onClose={onClose} />
          </div>
        ) : (
          <div className={styles.userButtons}>
            <button className={styles.userSignUp}>Sign up</button>
            <button className={styles.userSignIn}>Sign in</button>
          </div>
        )}
      </div>
    </div>
  );
}
