import React from "react";

import styles from "./Filters.module.css";

import {
  Checkbox,
  FormControlLabel,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Typography,
} from "@mui/material";
import { styled } from "@mui/material/styles";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

const StyledAccordion = styled(Accordion)(({ theme }) => ({
  boxShadow:
    "0px 1px 2px rgba(58, 58, 68, 0.24), 0px 2px 4px rgba(90, 91, 106, 0.24)",
  "&::before": {
    display: "none",
  },
  "&.Mui-expanded": {
    background: "#F7F7FA",
    boxShadow: "none",
  },

  // Header of Accordion

  "& .MuiAccordionSummary-root": {
    alignItems: "center",
    minHeight: "40px",
    paddingLeft: "12px",
    "&.Mui-expanded": {
      minHeight: "40px",
    },
    // title
    "& .MuiAccordionSummary-content": {
      margin: "0",
    },
    "& .MuiTypography-root": {
      fontFamily: "var(--Inter-Medium)",
      fontSize: "16px",
      lineHeight: "150%",
      letterSpacing: "0.022em",
      color: "#19191D",
    },

    // svg
    "& .MuiAccordionSummary-expandIconWrapper": {
      color: "#2264D1",
      "&.Mui-expanded": {
        color: "#4A4B57",
      },
    },
  },

  // details

  "& .MuiAccordionDetails-root": {
    display: "flex",
    flexDirection: "column",
    padding: "6px 8px 8px",
  },
}));

const StyledFormControlLabel = styled(FormControlLabel)(({ theme }) => ({
  "& .MuiFormControlLabel-label": {
    fontFamily: "var(--Inter-Regular)",
    fontSize: "16px",
    lineHeight: "150%",
    letterSpacing: "0.0275em",
    color: "#19191D",
  },
}));

const StyledCheckbox = styled(Checkbox)(({ theme }) => ({
  color: "#4A4B57",
  padding: "8px",
  marginRight: "8px",
  "&.Mui-checked": {
    color: "#2264D1",
  },
}));

const Filters = (props) => {
  return props.filters.map((item) => (
    <StyledAccordion className={styles.filter} key={item.title}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="panel1a-content"
        id="panel1a-header"
      >
        <Typography>{item.title}</Typography>
      </AccordionSummary>
      <AccordionDetails>
        {item.filters.map((filter) => (
          <StyledFormControlLabel
            key={filter.title}
            control={<StyledCheckbox />}
            label={filter.title}
          />
        ))}
      </AccordionDetails>
    </StyledAccordion>
  ));
};

export default Filters;
